.header__wrapper {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: space-between;
  align-items: center;

  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.header__navigation {
  display: flex;
  justify-content: baseline;
  gap: 100px;
  align-items: baseline;
}

.header__navlink {
  color: white;
  font-size: 30px;
  text-align: center;
}

.header__navlink:hover {
  color: rgb(193, 243, 113);
}
